import React from "react";
import { Navbar, Nav, Container, Badge, Button } from "react-bootstrap";
import { LinkContainer } from "react-router-bootstrap";
import { useNavigate } from "react-router-dom"; // Import useNavigate
import logo from "../../assets/images/logo2.jpg";
import { useAuth } from "../../context/AuthContext"; // Import useAuth

const Header = () => {
  const { isAuthenticated, logout, user, isAdmin } = useAuth(); // Get state and functions from context
  const navigate = useNavigate(); // Hook for navigation

  // Placeholder for cart count - replace with CartContext later
  const cartCount = 0;

  const handleLogout = () => {
    logout();
    navigate("/login"); // Redirect to login after logout
  };

  return (
    <Navbar bg="light" expand="lg" sticky="top" className="mb-3 shadow-sm">
      <Container>
        <LinkContainer to="/">
          <Navbar.Brand>
            <img
              src={logo}
              height="40"
              className="d-inline-block align-top"
              alt="E-Commerce Logo"
            />
          </Navbar.Brand>
        </LinkContainer>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="ml-auto">
            <LinkContainer to="/">
              <Nav.Link>Home</Nav.Link>
            </LinkContainer>
            <LinkContainer to="/shop">
              <Nav.Link>Shop</Nav.Link>
            </LinkContainer>

            {/* Show Admin link only if user is authenticated and is Admin */}
            {isAuthenticated && isAdmin() && (
              <LinkContainer to="/admin">
                <Nav.Link>Admin</Nav.Link>
              </LinkContainer>
            )}

            {isAuthenticated ? (
              <>
                <LinkContainer to="/order-history">
                  <Nav.Link>Orders</Nav.Link>
                </LinkContainer>
                {/* Display user's first name if available */}
                {user?.firstName && (
                  <Navbar.Text className="mx-2">
                    Hi, {user.firstName}
                  </Navbar.Text>
                )}
                {/* Use a Button or Nav.Link for logout */}
                <Button
                  variant="outline-secondary"
                  size="sm"
                  onClick={handleLogout}
                  className="ml-2"
                >
                  Logout
                </Button>
              </>
            ) : (
              <LinkContainer to="/login">
                <Nav.Link>Login</Nav.Link>
              </LinkContainer>
              // Optionally show Register link only when not logged in
              /* <LinkContainer to="/register">
                <Nav.Link>Register</Nav.Link>
              </LinkContainer> */
            )}

            <LinkContainer to="/cart">
              <Nav.Link>
                Cart{" "}
                {cartCount > 0 && (
                  <Badge pill bg="primary" text="light">
                    {cartCount}
                  </Badge>
                )}
              </Nav.Link>
            </LinkContainer>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
};

export default Header;
