import React from "react";
// REMOVE BrowserRouter import if it's not used elsewhere in this file
import { Route, Routes } from "react-router-dom";

// Layout
import Header from "./components/layout/Header";
// import Footer from './components/layout/Footer';

// Pages
import HomePage from "./pages/HomePage";
import LoginPage from "./pages/LoginPage";
import RegisterPage from "./pages/RegisterPage";
// ... other page imports if/when uncommented ...

// Simple Not Found component
const NotFound = () => (
  <div>
    <h2>404 Not Found</h2>
    <p>The page you requested does not exist.</p>
  </div>
);

function App() {
  // REMOVE the <Router> wrapper from here
  return (
    <>
      {" "}
      {/* Use Fragment or a div if needed, but Router is gone */}
      <Header /> {/* Header now inherits context from index.js */}
      <main className="container mt-3 mb-5">
        <Routes>
          {" "}
          {/* Routes now inherits context from index.js */}
          {/* Active Routes */}
          <Route path="/" element={<HomePage />} />
          <Route path="/login" element={<LoginPage />} />
          <Route path="/register" element={<RegisterPage />} />
          {/* Commented out unused routes */}
          {/* ... other routes ... */}
          {/* Fallback Route */}
          <Route path="*" element={<NotFound />} />
        </Routes>
      </main>
      {/* <Footer /> */}
    </> // End Fragment/div
  );
}

export default App;
