import React, { createContext, useState, useContext, useEffect } from "react";

const AuthContext = createContext(null);

export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null); // Store user info (email, name)
  const [token, setToken] = useState(localStorage.getItem("authToken")); // Load token initially
  const [isAuthenticated, setIsAuthenticated] = useState(
    !!localStorage.getItem("authToken")
  ); // Check if token exists

  useEffect(() => {
    // If token exists in storage, assume authenticated initially
    // You might want to add a check here to verify the token with the backend
    const storedToken = localStorage.getItem("authToken");
    if (storedToken) {
      setToken(storedToken);
      setIsAuthenticated(true);
      // Optionally decode token to get user info or fetch user profile from backend
      // For now, we'll just mark as authenticated
      // setUser({ email: 'user@from.token' }); // Example placeholder
    } else {
      setIsAuthenticated(false);
      setUser(null);
      setToken(null);
    }
  }, []);

  const login = (authData) => {
    // authData should be the response from your /api/auth/login endpoint
    // e.g., { email: '...', firstName: '...', token: '...', roles: [...] }
    localStorage.setItem("authToken", authData.token);
    setToken(authData.token);
    setUser({
      email: authData.email,
      firstName: authData.firstName,
      roles: authData.roles,
    }); // Store relevant user info
    setIsAuthenticated(true);
    console.log("User logged in:", authData.email);
  };

  const logout = () => {
    localStorage.removeItem("authToken");
    setToken(null);
    setUser(null);
    setIsAuthenticated(false);
    console.log("User logged out");
    // Optionally redirect to login page via navigate('/login') if needed elsewhere
  };

  const isAdmin = () => {
    return user?.roles?.includes("Admin") ?? false;
  };

  return (
    <AuthContext.Provider
      value={{ user, token, isAuthenticated, login, logout, isAdmin }}
    >
      {children}
    </AuthContext.Provider>
  );
};

// Custom hook to use the auth context easily
export const useAuth = () => {
  return useContext(AuthContext);
};
