import axios from "axios";

// Get the base URL from environment variables
const API_BASE_URL = process.env.REACT_APP_API_BASE_URL;

if (!API_BASE_URL) {
  console.error("Error: REACT_APP_API_BASE_URL is not defined in .env file.");
  // You might want to throw an error or provide a default for development
}

const apiClient = axios.create({
  baseURL: API_BASE_URL,
  headers: {
    "Content-Type": "application/json",
  },
});

// --- Axios Interceptor for adding JWT Token ---
// This runs before every request
apiClient.interceptors.request.use(
  (config) => {
    // Get the token from local storage (or wherever you store it)
    const token = localStorage.getItem("authToken"); // Adjust key name if needed
    if (token) {
      // If the token exists, add it to the Authorization header
      config.headers["Authorization"] = `Bearer ${token}`;
    }
    return config; // Continue with the request configuration
  },
  (error) => {
    // Handle request errors (e.g., network issues)
    return Promise.reject(error);
  }
);

